<template>
  <div class="page-login">
    <div class="login-header">
      <router-link to='/index' class="logo" href="http://www.meituan.com"></router-link>
    </div>
    <div class="login-panel">
      <div class="banner">
        <img
          src="https://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          alt=""
          width="480"
          height="370"
        />
      </div>
      <div class="form">
        <h4 class="tips" v-if="error">{{error}}</h4>
        <p>
          <span></span>
        </p>
        <el-input :class="{error:error&&!userName}" v-model="userName" placeholder="" prefix-icon="profile" type='text'></el-input>
        <el-input :class="{error:error&&!password}" v-model="password" placeholder="" prefix-icon="password" type='password'></el-input>
        <div class="foot">
          <a href="#"></a>
        </div>
        <el-button type="primary" class="btn-login" @click='submitFn'></el-button>
        <p class="reg">
          <span></span>
          <router-link to="/register"></router-link>
        </p>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title"></span>
          </h3>
          <div class="oauth cf">
            <span
              class="oauth__link oauth__link--qq"
              data-href="/account/connect/tencent"
              target="_blank"
              id="J-third-tencent"
            ></span>
            <span
              class="oauth__link oauth__link--weibo third-login-btn"
              data-href="/account/connect/sina"
              target="_blank"
              id="J-third-sina"
            ></span>
          </div>
        </div>
      </div>
    </div>
    <footer>
      <ul>
        <li>
          <a href='#'></a>
        </li>
        <li>
          <a href='#'></a>
        </li>
        <li>
          <a href='#'></a>
        </li>
        <li>
          <a href='#'></a>
        </li>
        <li>
          <a href='#'></a>
        </li>
      </ul>
      <p>2020  meituan.com ICP070791 11010502025545</p>
    </footer>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      error: '',
      userName: '',
      password: ''
    }
  },
  methods: {
    submitFn () {
      if (!this.userName) {
        this.error = ''
        return false
      }
      if (!this.password) {
        this.error = ''
        return false
      }
      api.getLogin({
        params: {
          userName: this.userName,
          password: this.password
        }
      })
        .then(res => {
          if (res.data.status === 'success') {
            this.$router.push('/index')
            this.$store.commit('setUserName', this.userName)
          } else {
            this.error = res.data.msg
          }
        })
    }
  }
}
</script>
<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>
